import ButtonCustom from "./../reuse/ButtonCustom";
import { ReactComponent as CartIcon } from "../../assets/images/icons/cartIcon.svg";

import { ReactComponent as MenuIcon } from "../../assets/images/icons/menuIcon.svg";
import { Link, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import LoggedInMenu from "./LoggedInMenu";
import { useAppSelector } from "../redux/store";
import SearchBar from "../reuse/SearchBar";
import Hamburger from "./Hamburger";

interface Props {
  sideBarstate: boolean;
  handleSidebar: (state: boolean) => void;
}

export default function Header2({ handleSidebar, sideBarstate }: Props) {
  const [showRightNav, setShowRightNav] = useState(true);
  const [showNav, setShowNav] = useState(true);
  const [home, setHome] = useState(false);

  const location = useLocation();
  const { user } = useAppSelector((state) => state.user);
  const { productParams } = useAppSelector((state) => state.product);
  const [scrolled,setScrolled]= useState(false);



  



  const regex = /(shop)\/\d/g;

  const okRoute = regex.test(location.pathname);

  useEffect(() => {
    const noNavRoutes = ["/register", "/login"];
    if (
      location.pathname === "/" ||
      location.pathname === "/shop" ||
      noNavRoutes.includes(location.pathname)
    ) {
      setHome(true);
    } else {
      setHome(false);
    }

    if (noNavRoutes.includes(location.pathname)) {
      setShowRightNav(false);
    } else {
      setShowRightNav(true);
    }

    //checkNoRoute();

    /*if (okRoute) {
      setShowNav(false);
    } else {
      setShowNav(true);
    }*/

    console.log(okRoute);
  }, [location.pathname, okRoute]);




  const handleScroll=() => {
    const offset=window.scrollY;
    if(offset > 200 ){
      setScrolled(true);
    }
    else{
      setScrolled(false);
    }
  }

  useEffect(() => {
    window.addEventListener('scroll',handleScroll)

    return () => {
      setScrolled(false)
    };
  },[])

  return (
    <header
      className={` 
      ${scrolled ? "!fixed top-0 left-0":""}
      ${showNav ? `flex` : `hidden`} relative inset-0 z-10 h-12 flex-row items-center justify-between px-8 py-11 lg:px-24 text-white 
      bg-white/80
      ${showRightNav && ' !text-black' } 
      transition-all duration-700 ease-in
      `}
      
    >
      <div
        className="flex items-center justify-center gap-x-6"
      >
        {/*<div
          className="menuIcon cursor-pointer z-20"
          onClick={(e) => handleSidebar(!sideBarstate)}
        >
         { /*<MenuIcon className="h-6" />*}
          <Hamburger sideBarstate={sideBarstate}/>
        </div>*/}
        <div className="font-[Fujimaru] ml-12">
          <Link to="/">Anime Clans</Link>
        </div>
      </div>
      

      {showRightNav && (
        <>

<ul className="flex items-center gap-x-8 whitespace-nowrap break-words md:text-lg lg:text-xl">
        

        {user && user.roles?.includes("Admin") && (
          <li className="">
            <Link to="inventory">Inventory</Link>
          </li>
        )}

        <li className="hidden md:inline-block">
          <Link to="shop">Shop</Link>
        </li>

        <li className="hidden md:inline-block">
          <Link to="about">About Anime Clans</Link>
        </li>

        <li className="hidden md:inline-block">
          <Link to="shop">Pre-orders</Link>
        </li>

        <li className="hidden xl:inline-block">
          <Link to="wishlist">WishList</Link>
        </li>
      </ul>
          {/* <div className="flex  items-center mx-3">
            <SearchBar />
          </div>*/}

          <ul className="hidden md:flex items-center">
            <Link to="cart">
              <li className="mx-2">
                <ButtonCustom
                  name="Cart"
                  buttonClass="bg-white hover:!bg-slate-300 focus:!bg-slate-300 focus:shadow-lg active:!bg-slate-300
                   active:shadow-lg text-black !text-lg  flex justify-between p-4"
                  Logo={CartIcon}
                />
              </li>
            </Link>

            {user === null ? (
              <li className="mx-2">
                <Link to="login" className="decoration-none">
                  <ButtonCustom
                    name="Login"
                    buttonClass={`  bg-black/80
                    hover:!bg-black focus:!bg-black focus:shadow-lg active:bg-black active:shadow-lg  
                    text-white !text-lg
                    flex justify-between p-8`}
                  />
                </Link>
              </li>
            ) : (
              <li className="ml-2 lg:px-4">
                {/*<LoggedInMenu/>*/}
                <LoggedInMenu name={user.username} home={home} />
              </li>
            )}
          </ul>
        </>
      )}
    </header>
  );
}
