import * as yup from 'yup';

export const reviewValidationSchema = yup.object({
    rating: yup.number().required("Please enter a star rating for this review").moreThan(1,"Please enter a star rating for this review").lessThan(5,"Please enter a star rating for this review"),
    name: yup.string().required(),
    email: yup.string().required(),
    title: yup.string().required(),
    userReview: yup.string().required(),
})


