import DeliveryDeets from "./DeliveryDetails";
import OrderHistory from "./OrderHistory";
import PersonalInfo from "./PersonalInfo";

const userTabs = ["Personal Information", "Delivery Details", "Order History"];

export default function UserProfile() {
  
  return (
  
        <div className="flex flex-col md:flex-row my-10 md:my-20 mx-3 md:mx-0 w-full">
          {/**left side */}
          <div className="w-full md:w-1/5">

            <ul className="relative">

                {userTabs.map((item, index) => {
                    return(
                        <li key={index} className="w-fit md:w-full text-sm md:text-lg mb-4 px-4 py-4 md:pr-2  bg-red-500 rounded-md cursor-pointer relative text-white">
                            <h6 className="md:w-max md:pl-8 pr-3 md:absolute right-0 top-0 bottom-0">{item}</h6>
                        </li>
                    );
                })}
            </ul>
          </div>

          {/**left side */}

          {/**right side */}
          <div className="w-full md:w-3/5 md:ml-14 md:mr-40">


          <PersonalInfo/>


          <DeliveryDeets/>

          <OrderHistory/>

          </div>

          {/**right side */}
        </div>
      

  );
}
